
#app {
  font-family: "Raleway", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #111;
  background-color: white;
}
span {
  display: block;
  margin-bottom: 10px;
}
header {
  position: relative;
  top: 0px;
  margin-left: 10px;
  width: calc(100% - 20px);
  z-index: 900;
  background-color: #333;
  box-shadow: 0px 2px 10px #888;
  color: white;
  border-radius: 0px 0px 10px 10px;
  height: 4.5em;
}
header span {
  padding-top: 0.5em;
  font-size: 2em;
  font-weight: bold;
}
header img {
  padding-top: 0.5em;
  padding-left: 0.5em;
  float: left;
  height: 80%;
}
#time {
  padding-top: 10px;
}
.scroll {
  overflow: auto;
}
.fullpage {
  position: fixed;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  z-index: 950;
}
.menu {
  z-index: 990;
  box-shadow: 0px 2px 10px #444;
  background-color: #FFF;
}
.menu:after {
  content:'';
  position: absolute;
  top: 0px;
  left: 13px;
  margin-top: -10px;
  width: 0;
  height: 0;
  border-bottom: solid 10px white;
  border-left: solid 10px transparent;
  border-right: solid 10px transparent;
}
.menu span {
  text-align: left;
  padding: 20px;
  margin: 0px;
  font-size: 1.2em;
  cursor: pointer;
}
.menu span img {
  width: 1em;
  height: 1em;
  float: left;
  margin-top: 2px;
  padding-right: 10px;
}
.shadow {
  background-color: rgba(0,0,0,0.5);
}
.dialog {
  position: fixed;
  top: 20%;
  left: 20%;
  z-index: 1000;
  width: 60%;
  max-width: 400px;
  padding-bottom: 20px;
  box-shadow: 0px 2px 10px #444;
  background-color: #FFF;
}
.dialog button {
  width: 90%;
  height: 10%;
  background-color: #555;
  color: #EEE;
  font-size: 1.2em;
  border: none;
  text-align: center;
  text-decoration: none;
  box-shadow: 0px 2px 5px #888888;
  padding: 4px;
  margin: 10px;
  margin-top: 20px;
}
.dialog img {
  display: inline;
  width: 2em;
  height: 2em;
}
.dialog span {
  position: relative;
  top: -8px;
  left: 8px;
  display: inline;
}
@media (min-aspect-ratio: 4/3) {
  /* LANDSCAPE */
.menu {
    position: fixed;
    top: 4.8em;
    left: calc(23px + 100vh);
}
#field {
    position: fixed;
    background-color: #FAFAFA;
    overflow: hidden;
    top: 0px;
    left: 0px;
    min-width: 50vw;
    width: 100vh;
    max-width: 70vw;
    height: 100vh;
}
#flow {
    position: fixed;
    right: 0px;
    top: 0px;
    min-width: 30vw;
    width: calc(100vw - 100vh);
    max-width: 50vw;
    height: 100vh;
    box-shadow: 0px 0px 3px #444;
    background-color: white;
    z-index: 5;
    overflow: auto;
}
}
@media (max-aspect-ratio: 4/3) {
  /* PORTRAIT */
.menu {
    position: absolute;
    top: 5em;
    left: 20px;
}
#field {
    position: fixed;
    background-color: #FAFAFA;
    overflow: hidden;
    box-shadow: 0px 0px 2px #444 inset;
    top: 7em;
    left: 0px;
    width: 100vw;
    height: 100vw;
    z-index: 200;
}
#flow {
    position: fixed;
    left: 0px;
    top: 0px;
    width: 100%;
    height: 100%;
    box-shadow: 0px 0px 3px #444;
    background-color: white;
    z-index: 5;
    overflow: hidden;
}
#buttons {
    margin-top: calc(100vw + 1em);
    height: calc(100vh - 100vw - 7em);
    overflow: auto;
}
  /*#buttons {
    position: absolute;
    left: 0px;
    top: calc(100vw + 7.5em);
    width: 100vw;
    height: 30%;
  }
  #time {
    position: absolute;
    left: 0px;
    top: 5.4em;
    width: 100vw;
  }*/
}
@media print {
#buttons {
    visibility: collapse;
}
#time {
    visibility: collapse;
}
}




